/**
 * External imports
 */
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700');
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
main,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
q:before,
blockquote:after,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/**
 * General
 */
body {
  font-family: 'Montserrat', sans-serif;
}
*,
::before,
::after {
  box-sizing: border-box;
}
/**
 * Hero/Intro
 */
/**
 * Hero/Intro for the page
 * Mobile then Desktop
 */
.hero {
  background: #3a2a2a url('images/hero.png') no-repeat center bottom;
  background-size: 105%;
  height: 100vh;
  position: relative;
  z-index: 1;
}
.hero .logo {
  position: absolute;
  top: 55px;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media screen and (orientation: landscape) {
  .hero .logo {
    top: 30px;
  }
}
.hero .lead {
  font-weight: 700;
  color: #d57a46;
  text-align: center;
  text-transform: uppercase;
  font-size: 28px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
  width: 100%;
}
@media screen and (orientation: landscape) {
  .hero .lead {
    transform: translate(-50%, -75%);
  }
}
.hero .animation-wrapper {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: auto;
  left: 50%;
  bottom: 0;
  overflow: hidden;
  transform: translateX(-50%);
}
@media screen and (orientation: landscape) {
  .hero .animation-wrapper {
    width: 40%;
  }
}
.hero .animation-wrapper img {
  position: absolute;
  animation-name: heroIntro;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  animation-iteration-count: 1;
  animation-fill-mode: both;
  z-index: 1;
}
.hero .animation-wrapper .phone {
  display: block;
  width: 50%;
  height: auto;
  margin: 0 auto;
  position: relative;
  margin-bottom: -72px;
  animation-delay: 250ms;
  z-index: 2;
}
.hero .animation-wrapper .ss1 {
  top: 60px;
  left: 58%;
  width: 40%;
  animation-delay: 650ms;
}
.hero .animation-wrapper .ss2 {
  top: 12px;
  left: 50%;
  margin-left: -47%;
  width: 40%;
  animation-delay: 450ms;
}
@keyframes heroIntro {
  0% {
    transform: translateY(50%);
    opacity: 0;
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}
/**
 * Description
 */
/**
 * Description block
 * With download link
 * Mobile then Desktop
 */
.description {
  min-height: 100vh;
  padding: 46px 32px;
}
.description .download-btn {
  display: block;
  width: 172px;
  height: auto;
  margin: 0 auto 77px;
}
.description .download-btn img {
  display: block;
  width: 100%;
}
.description .title {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 24px;
}
.description .title small {
  font-size: 12px;
  font-weight: 400;
  color: #c17144;
  display: block;
  margin-bottom: 10px;
}
.description .text-content {
  font-size: 12px;
  color: #929292;
  line-height: 2;
}
.description .text-content:first-of-type {
  margin-bottom: 22px;
}
